import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";



@IonicPage()
@Component({
  selector: 'page-wimax-recharge04',
  templateUrl: 'wimax-recharge04.html',
})
export class WimaxRecharge04Page {

  refID;
  amount;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public gaProvider : GoogleAnalyticsProvider) {
    this.refID = navParams.get("refID");
    this.amount = navParams.get("amount");
  }

  goToWelcome = function () {
    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.popToRoot();
  };

  rechargeAnother = function () {

    this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.push('wimaxRecharge01');
  };


  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Wimax Recharge Successful");
  }

}



